@font-face {
    font-family: 'Dosis';
    src: url('../assets/fonts/Dosis-Bold.ttf') format('truetype');
    font-weight: 600;
    font-style: normal;
}
@font-face {
    font-family: 'Dosis';
    src: url('../assets/fonts/Dosis-ExtraBold.ttf') format('truetype');
    font-weight: 700;
    font-style: normal;
}
@font-face {
    font-family: 'Dosis';
    src: url('../assets/fonts/Dosis-ExtraLight.ttf') format('truetype');
    font-weight: 100;
    font-style: normal;
}
@font-face {
    font-family: 'Dosis';
    src: url('../assets/fonts/Dosis-Light.ttf') format('truetype');
    font-weight: 200;
    font-style: normal;
}
@font-face {
    font-family: 'Dosis';
    src: url('../assets/fonts/Dosis-Medium.ttf') format('truetype');
    font-weight: 300;
    font-style: normal;
}
@font-face {
    font-family: 'Dosis';
    src: url('../assets/fonts/Dosis-Regular.ttf') format('truetype');
    font-weight: 400;
    font-style: normal;
}
@font-face {
    font-family: 'Dosis';
    src: url('../assets/fonts/Dosis-SemiBold.ttf') format('truetype');
    font-weight: 500;
    font-style: normal;
}
* {
    margin: 0;
    padding: 0;
    font-family: 'Dosis';
    font-weight: 100;
    font-size: normal;
}
h1 {
    font-size: 80px;
}
h2 {
    font-size: 50px;
}
ul {
    list-style-type: none;
}
body {
    background-color: whitesmoke;
    height: 100vh;
    overflow: hidden;
    position: relative;
}
.highlight {
    color: lightblue;
    font-weight: 300;
}
nav {
    position: absolute;
    bottom: 5rem;
    left: 5rem;
    z-index: 1;
}
nav ul {
    list-style: none;
}
nav li {
    font-size: 5rem;
}
nav li:hover {
    font-weight: 200;
}

#content {
    position: relative;
    width: 100vw;
    left: 40vw;
    top: 30vh;
    height: 70vh;
    /* height: 60vh; */
    /* top: 20vh; */
}

#about,
#skills,
#projects,
#contact {
    position: absolute;
    height: 100%;
    width: 100%;
    visibility: hidden;
}

#skills ul li {
    font-size: 24px;
    padding: 1em 1em 0 0;
}

.project ul li {
    font-size: 24px;
    padding-top: 5px;
    padding-top: 5px;
}
.project li:last-of-type {
    padding-bottom: 20px;
}
.project a {
    text-decoration: none;
}

#stan {
    display: block;
    max-height: 400px;
    max-width: 400px;
    border-radius: 50%;
}
#contact a {
    text-decoration: none;
    color: lightblue;
    font-weight: 300;
}

@media only screen and (max-width: 960px) {
    nav {
        position: absolute;
        align-items: center;
        bottom: auto;
        left: auto;
        z-index: 1;
    }
    nav ul {
        width: 100vw;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    nav li {
        font-size: 48px;
        padding: 20px;
    }
    #content {
        position: relative;
        left: 20vw;
        width: 80vw;
        top: 30vh;
        height: 70vh;
        /* height: 60vh; */
        /* top: 20vh; */
    }
}
